
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
	data() {
		return {
			confirm: false,
			remarks: '',
			list: [{
				DataType:"100",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"200",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"201",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"300",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"400",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"500",
				MaxValue:'',
				MinValue:''
			},{
				DataType:"501",
				MaxValue:'',
				MinValue:''
			}]
		}
	},
	methods: {
		
		submitData() {
			let reg=/^[0-9]+\.{0,1}[0-9]{0,2}$/; //只能输入数字和小数点
			for(let i=0;i<this.list.length;i++){
				if(this.list[i].MinValue>this.list[i].MaxValue){
					this.$vux.toast.show({
						text: this.$common.warnDataType[this.list[i].DataType]+'（下限）不能大于（上限）',
						type: 'cancel'
					});
					return false;
				}
				if(!reg.exec(this.list[i].MinValue)){
					this.list[i].MinValue='';
					this.$vux.toast.show({
						text: this.$common.warnDataType[this.list[i].DataType]+'（下限）只能输入数字',
						type: 'cancel'
					});
					return false;
				}
				if(!reg.exec(this.list[i].MaxValue)){
					this.list[i].MaxValue='';
					this.$vux.toast.show({
						text: this.$common.warnDataType[this.list[i].DataType]+'（上限）只能输入数字',
						type: 'cancel'
					});
					return false;
				}
			}				
			this.$common.ajax({
				data: {
					header: {
						action: 'EditHealthWarnSetting',
					},
					body: {
						UserID: this.$route.query.userId,
						DoctorID: this.$common.getLocalStorage('user').doctorid,
						Remark: this.remarks,
						Datas: this.list
					}
				},
				success: (res) => {
					this.$vux.toast.show({
						text: '修改成功'
					});
				}
			})
		},
		getData() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetHealthWarnSetting',
					},
					body: {
						UserID: this.$route.query.userId
					}
				},
				success: (res) => {
					this.list = res;
				}
			})
		}
	},
	mounted() {
		this.getData();
	},

	components: {
		Group,
		Cell,
		XHeader,
		XInput,
		Confirm,
		XTextarea
	}
}



